@charset "UTF-8";
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body,
html {
  height: 100%;
  scroll-padding-top: 80px;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #fff;
  overflow-x: clip;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
div,
span,
input,
textarea,
select,
button,
a,
ul,
ol,
li {
  font-family: "Montserrat", sans-serif;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;
}

.navbar {
  position: fixed;
  display: flex;
  align-items: center;
  top: 0;
  width: 100%;
  background-color: #fff;
  height: 80px;
  border-bottom: 2px solid #000;
  z-index: 300;
}
.navbar a {
  text-decoration: none;
  color: #000;
}
.navbar__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5em;
}
.navbar__logo img {
  height: 60px;
}
.navbar__logo-text {
  font-size: 2.5rem;
  font-weight: 600;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  padding: 0.8em 0.2em;
}
.navbar__list {
  position: relative;
  display: none;
  gap: 3em;
}
.navbar__list-item {
  position: relative;
  font-size: 2.2rem;
  font-weight: 500;
}
.navbar__list-item:hover::after {
  width: 100%;
}
.navbar__list-item::after {
  transition: width 0.5s;
  position: absolute;
  content: "";
  bottom: -10px;
  left: 0;
  background-color: #f0eb77;
  height: 4px;
  width: 0;
}
.navbar__nav {
  display: flex;
  align-items: center;
  padding: 2em;
  justify-content: space-between;
}

.burger {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

@media (max-width: 991px) {
  .navbar__list.burger-active {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(239, 238, 238);
    background-color: transparent;
    backdrop-filter: blur(25px);
    position: absolute;
    top: 80px;
    left: 0;
    height: calc(100vh - 80px);
    width: 100%;
  }
  .navbar__list.burger-active a {
    color: #fff;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
    text-transform: uppercase;
  }
  .no-scroll {
    overflow: hidden;
  }
  .navbar__logo-text {
    display: none;
  }
}
.line {
  width: 3em;
  height: 0.3em;
  background-color: black;
  margin: 0.3em;
  border-radius: 20px;
  transition: 0.3s ease-in-out;
}

.header {
  position: relative;
  height: calc(75dvh - 80px);
  display: flex;
  min-height: 600px;
  margin-top: 80px;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-image: url("../../dist/img/header2Small.webp");
  background-size: cover;
  background-position: center;
  z-index: 10;
}
.header::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: -1;
}
.header__box {
  width: 95%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 3em;
  margin: 0 auto;
  color: #fff;
}
.header__box-buttons {
  display: flex;
  flex-direction: column;
  gap: 2em;
}
.header__box-buttons--first {
  border: 2px solid #fff;
  transition: border 0.2s, color 0.2s;
  background-color: transparent;
  backdrop-filter: blur(5px);
}
.header__box-buttons--first:hover {
  color: #f0eb77;
  border: 2px solid #f0eb77;
}
.header__box-buttons--second {
  transition: color 0.2s;
}
.header__box-buttons--second:hover {
  color: #f0eb77;
}
.header__box-heading {
  font-size: 2.4rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  font-weight: 600;
  margin-bottom: 0.3em;
}
.header__box-heading span {
  color: #f0eb77;
}
.header__box-text {
  font-size: 1.6rem;
  font-weight: 400;
}
.header__box-button {
  font-size: 1.4rem;
  color: #fff;
}

.footer {
  position: relative;
  background-color: #323232;
  color: #fff;
  overflow: clip;
}
.footer__copyright {
  position: relative;
  text-align: center;
  padding: 2em 0;
  color: #fff;
  font-size: 1.4rem;
  border-top: 2px solid #000;
  z-index: 300;
  background-color: #323232;
}
.footer__row {
  display: flex;
  flex-direction: column;
  padding: 2em;
  width: 90%;
  gap: 2em;
}
.footer__row p {
  font-size: 1.6rem;
}
.footer__row--middle {
  justify-content: center;
  align-items: center;
  background-color: #212121;
}
.footer__box {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4em 0;
  gap: 4em;
  z-index: 3;
}
.footer__box-shape--first {
  bottom: -30px;
  left: -20px;
  background-color: #f0eb77;
  transform: rotate(45deg);
}
.footer__box-shape--second {
  top: -40px;
  right: -40px;
  background-color: #323232;
  border: 2px solid #fff;
  border-radius: 50%;
  transform: rotate(45deg);
}

.section__box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  font-weight: 600;
  padding: 4em 1em;
}
.section__box-heading {
  position: relative;
  margin: 1em 0;
  color: #000;
  font-size: 2.2rem;
  text-transform: uppercase;
}
.section__box-heading::before, .section__box-heading::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 50%;
}
.section__box-heading::before {
  top: -15px;
  left: -30px;
  border-top: 4px solid #000;
  border-left: 4px solid #000;
}
.section__box-heading::after {
  bottom: -15px;
  right: -30px;
  border-bottom: 4px solid #000;
  border-right: 4px solid #000;
}
.section__box-text {
  color: #686868;
  font-size: 1.4rem;
}

.about__img {
  width: 100%;
  padding: 0 2em;
}
.about__img img {
  width: 100%;
  height: auto;
  max-height: 350px;
  object-fit: cover;
  object-position: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.about__text {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 2em;
  font-size: 1.4rem;
  font-weight: 500;
  width: 100%;
  gap: 1.5em;
  line-height: 30px;
}
.about__text h3 {
  position: relative;
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
}
.about__text h3::after {
  position: absolute;
  content: "";
  bottom: -5px;
  left: 0;
  width: 22%;
  height: 4px;
  background-color: #f0eb77;
}
.about__text p {
  font-size: 1.4rem;
  font-weight: 500;
}
.about__text--right {
  padding: 4em 2em;
}
.about__us {
  position: relative;
  overflow: hidden;
}
.about__us-box {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.main__values {
  position: relative;
  background-image: url("../../dist/img/architecture-1868667_1920.webp");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
  padding: 8em 0;
  gap: 3em;
  margin: 8em 0;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.main__values::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: -1;
}
.main__values h3 {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-size: 2rem;
  margin-bottom: 2em;
  font-weight: 500;
}
.main__values h3::after {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 15%;
  height: 4px;
  background-color: #f0eb77;
}
.main__values-list {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-top: 4em;
  padding: 4em 6em;
  gap: 3em;
}
.main__values-list::after {
  position: absolute;
  content: "";
  bottom: -30px;
  left: 40px;
  width: 35%;
  height: 2px;
  background-color: #f0eb77;
}
.main__values-list--item {
  font-size: 1.6rem;
  color: #fff;
}
.main__values-list--item span {
  font-weight: 600;
  color: #f0eb77;
}

.our_mission {
  position: relative;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  z-index: 10;
}

.our__commitment {
  position: relative;
  padding: 6em 0;
  background-image: url("../../dist/img/ourExperience.webp");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.our__commitment::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: -1;
}
.our__commitment-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 75%;
  margin: 0 auto;
  gap: 2em;
}
.our__commitment-box p {
  font-variant: small-caps;
  font-weight: 500;
  line-height: 35px;
  color: #fff;
  font-size: 1.4rem;
  text-align: center;
  text-transform: uppercase;
}
.our__commitment-button {
  font-size: 1.4rem;
  color: #fff;
  border: 2px solid #fff;
}

.services {
  position: relative;
  opacity: 0;
  overflow: hidden;
}
.services__cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5em;
  width: 90%;
  margin: 0 auto;
  margin-top: 5em;
}
.services__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 85%;
  max-width: 300px;
  min-height: 350px;
  padding: 2em 2em;
  background-position: center;
  background-size: cover;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  z-index: 10;
}
.services__card p {
  position: relative;
  margin-top: 1em;
  font-size: 1.4rem;
  font-weight: 600;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}
.services__card p::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 4px;
  background-color: #d9d9d9;
}
.services__card-button {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  width: max-content;
  font-size: 1.4rem;
  color: #fff;
}
.services__card::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.services__card--first {
  background-image: url("../../dist/img/home1.webp");
}
.services__card--second {
  background-image: url("../../dist/img/home2.webp");
}
.services__card--third {
  background-image: url("../../dist/img/home3.webp");
}

.portfolio {
  position: relative;
  z-index: 3;
  background-color: #fdfdfd;
}
.portfolio__projects {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 4em;
  height: 100%;
  gap: 6em;
}
.portfolio__project {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
  gap: 6em;
  width: 100%;
  background-size: cover;
  height: 100%;
  opacity: 0;
  transition: opacity 0.7s;
  background-position: center;
  text-align: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  border: 2px solid #000;
  z-index: 10;
}
.portfolio__project-text {
  width: 90%;
  margin: 0 auto;
  margin-top: 2em;
  color: #fff;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
}
.portfolio__project-description {
  position: relative;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  margin-top: 2em;
  color: #f4f4f4;
  font-size: 1.4rem;
  margin-bottom: 2em;
  padding: 1em;
  background-color: rgba(73, 73, 73, 0.6470588235);
  border-bottom: 2px solid #000;
  border-top: 2px solid #000;
  backdrop-filter: blur(5px);
  z-index: 30;
}
.portfolio__project::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.portfolio__project--first {
  background-image: url("../../dist/img/portfolio1.webp");
}
.portfolio__project--second {
  background-image: url("../../dist/img/portfolio2.webp");
}
.portfolio__project--third {
  background-image: url("../../dist/img/portfolio3.webp");
}
.portfolio__project--fourth {
  background-image: url("../../dist/img/portfolio4.webp");
}
.portfolio__project--fifth {
  background-image: url("../../dist/img/portfolio5.webp");
}
.portfolio__project--sixth {
  background-image: url("../../dist/img/portfolio6.webp");
}

.numbers {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  border-top: 2px solid #f4f4f4;
  padding: 3em 0;
}

.our__scores {
  width: 85%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.score {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2em 0;
  gap: 2em;
}
.score__img {
  width: 50px;
}
.score__text {
  text-align: center;
  font-size: 1.4rem;
  font-weight: bold;
}
.score__text span {
  color: #a939d5;
}
.score__number {
  font-size: 2.8rem;
  font-weight: bold;
}

.faq {
  overflow-x: clip;
}

.accordion {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 5em;
  background-color: #fff;
}
.accordion__contact {
  margin-top: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2em;
}
.accordion__contact-text {
  font-size: 1.6rem;
  text-transform: capitalize;
  font-weight: 600;
  text-align: center;
}
.accordion__contact-button {
  font-size: 1.6rem;
  color: #000;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}
.accordion-article {
  display: none;
  flex-direction: column;
  gap: 2em;
}
.accordion-box {
  background-color: #f7f7f7;
}
.accordion-btn {
  width: 100%;
  padding: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: none;
  text-align: left;
  border: none;
  cursor: pointer;
  font-size: 1.6rem;
}
.accordion-btn img {
  height: 40px;
}
.accordion-btn-category {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: none;
  outline: none;
  border: none;
  width: 100%;
  padding: 1em 0;
  text-align: left;
  font-size: 1.8rem;
  font-weight: 600;
  text-transform: capitalize;
  cursor: pointer;
}
.accordion-btn-category:hover span::after {
  width: 100%;
}
.accordion-btn-category span {
  position: relative;
}
.accordion-btn-category span::after {
  transition: width 1s;
  position: absolute;
  content: "";
  bottom: -10px;
  left: 0;
  width: 35%;
  height: 4px;
  background-color: #f0eb77;
}
.accordion-info {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}
.accordion-info-text {
  padding: 2em;
  font-size: 1.4rem;
}

.accordion-info.active {
  max-height: 1003px;
}

.references {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5em;
}

.slider {
  width: 100vw;
  margin: auto;
  position: relative;
}

.slides {
  user-select: none;
  display: flex;
  margin: 1em auto;
  transition: transform 0.5s ease;
}

.slider .slide:not(.active) .card3 {
  filter: blur(5px);
}

.slide {
  min-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.5s ease;
}

.xd2 {
  border-radius: 50%;
  width: 100%;
  height: auto;
  max-height: 150px;
  overflow: hidden;
  object-fit: cover;
}

.card3 * {
  user-select: none;
}

.xd2 img {
  width: 100%;
  height: 100%;
  max-height: 150px;
  object-fit: cover;
  object-position: center;
  border: 2px solid #000;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.card3 {
  position: relative;
  text-align: center;
  width: 90%;
  max-width: 400px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 14em auto;
  transition: filter 0.5s ease;
  cursor: grab;
}

.avatar3 {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto 15px;
  margin-bottom: 2em;
}

.avatar3 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.review {
  position: relative;
  font-style: italic;
  margin-bottom: 2em;
  width: 90%;
  margin: 2em auto;
  padding: 1em;
  line-height: 30px;
  font-size: 1.4rem;
}
.review::after {
  position: absolute;
  content: "“";
  font-size: 8rem;
  color: #000;
  top: 0;
  left: -30px;
}
.review::before {
  position: absolute;
  content: "”";
  font-size: 8rem;
  color: #000;
  bottom: -30px;
  right: -10px;
}

.name {
  position: relative;
  font-weight: bold;
  font-size: 1.6rem;
  margin-bottom: 1em;
}
.name::after {
  position: absolute;
  content: "";
  background-color: #f0eb77;
  bottom: -10px;
  left: 50%;
  width: 30%;
  height: 4px;
  transform: translateX(-50%);
}

.slide-content {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 20px;
  gap: 2em;
  position: relative;
}

.slide-content-text {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
  height: 100%;
  gap: 4em;
}
.slide-content-text h2 {
  position: relative;
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1px;
}
.slide-content-text h2::after {
  content: "";
  position: absolute;
  width: 15%;
  height: 4px;
  background-color: #f0eb77;
  bottom: -5px;
  left: 0;
}
.slide-content-text p {
  font-size: 1.5rem;
}

.arrow {
  background: transparent;
  border: none;
  color: black;
  padding: 10px;
  cursor: pointer;
  z-index: 100;
  font-size: 6rem;
}

.arrow-prev {
  left: 0;
}

.arrow-next {
  right: 0;
}

.dots {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 20px;
  margin: 2em auto 0;
}

.dot {
  height: 10px;
  width: 10px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin: 0 5px;
  cursor: pointer;
}

.reference__bottom {
  display: flex;
  flex-direction: row;
